<template>
  <div class="voting-wrapper">
    <div class="voting-container">
      <div class="header">
        {{ questionData.question }}
      </div>
      <div class="subheader">
        Можно отметить только один пункт
      </div>
      <div
        v-if="!isVoted"
        class="answers-container"
      >
        <div class="answer-item"
          v-for="answer in questionData.variants" 
          :key="answer.id"
          @click="answerClick(answer.id)"
          :class="{'background-blue': selectedAnswerId===answer.id}"
        >
          <div class="answer-item-text">
            {{ answer.name }}
          </div>
        </div>
      </div>

      <div
        v-if="isVoted"
        v-html="'Спасибо!<br>Ваш голос учтен'"
        class="is-voted-text"
      >
      </div>

      <div class="voting-button-container">
        <div
          v-if="!isVoted"
          @click="vote"
          :class="{'enable': selectedAnswerId}"
          class="voting-button"
        >
          ПРОГОЛОСОВАТЬ
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  data() {
    return {
      serverURL: "https://dev1.med-game.ru/api/v1",
      questionData: {},
      selectedAnswerId: null,
      isVoted: false
    }
  },
  computed: {
    
  },
  methods: {
    getQuestionData(){
      axios({
        method: 'get',
        url: `${this.serverURL}/voting/${this.$route.params.sessionId}`,
      })
      .then((questionData) => {
        console.log(questionData);
        this.questionData = questionData.data.data
      })
      .catch((error) => {
        console.log('Ошибка:');
        console.log(error)
        return error
      })
    },
    answerClick(id){
      if (this.selectedAnswerId === id) {
        this.selectedAnswerId = null
      }
      else {
        this.selectedAnswerId = id
      }
    },
    vote(){
      axios({
          url: `${this.serverURL}/voting/${this.$route.params.sessionId}/answer`,
          method: 'post',
          data: {
            "variant_id": this.selectedAnswerId
          }
        })
        .then(() => {
          this.isVoted = true
          // localStorage.setItem("isVoted", 1)
          localStorage.setItem(this.$route.params.sessionId, 1)          
        })
        .catch(function (error) {
          console.log(error);
        })    
    }

  },
  mounted() {
    this.getQuestionData()
    this.isVoted = false
    // const value = localStorage.getItem(this.$route.params.sessionId)
    // if (value == 1) {
    //   this.isVoted = true
    // }
    // else {
    //   this.isVoted = false
    // }
  },
}
</script>

<style>
body{
  margin: 0;
}
.voting-wrapper{
  display: flex;
  justify-content: center;
  margin: 0, auto;
  background: #F5F5F5;
  height: 100vh;
}
.voting-container{
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 320px;
  background: #FFFFFF;  
  height: 100%;
  overflow-y: auto;
}
.header{
  font-family: 'Open Sans', sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 20px;
  line-height: 27px;
  text-align: center;
  color: #224A81;
  margin-top: 50px;
  margin-bottom: 20px;
}
.subheader{
  display: flex;
  justify-content: center;
  font-family: 'Open Sans', sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 19px;
  color: #000000;
  margin-bottom: 50px;
}
.answers-container{
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 10px;
  /* margin-bottom: 220px; */
  margin-bottom: 50px;
}
.answer-item{
  display: flex;
  align-items: center;
  width: 300px;
  min-height: 43px;
  border: 0.5px solid #000000;
  background: #FFFFFF;
  color: #000000;  
  cursor: pointer;
}
.background-blue{
  background: #285798;
  color: #FFFFFF;
}
.answer-item-text{
  margin-left: 30px;
  margin-right: 30px;
  margin-top: 5px;
  margin-bottom: 5px;
  font-family: 'Open Sans', sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 19px;
}
.voting-button-container{
  display: flex;
  align-items: end;
  flex-grow: 1;
}
.voting-button{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 300px;
  height: 43px;
  border-radius: 20px;
  cursor: pointer;
  background: #D9D9D9;
  font-family: 'Open Sans', sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 19px;
  text-align: center;
  color: #000000;
  pointer-events: none;
  margin-bottom: 50px;
}
.voting-button-text{
  font-family: 'Open Sans', sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 19px;
  text-align: center;
  color: #000000;
}
.enable {
  pointer-events: auto;
  background: #092244;
  color: #FFFFFF;
}
.is-voted-text{
  display: flex;
  justify-content: center;
  margin-top: 120px;
  font-family: 'Open Sans', sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  text-align: center;
  color: #000000;
}

</style>